/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useRef} from 'react'
import {useIntl} from 'react-intl'
import {Link, withRouter} from 'react-router-dom'
import {KTSVG} from '../../../_metronic/helpers'
import {PageTitle} from '../../../_metronic/layout/core'
import moment from 'moment'
import {Button, Row, Col } from 'antd'
import {mainConfig} from '../../../config/mainConfig'

let token = ''

function ProductSupplierWrapper(props: any) {
  const intl = useIntl()

  const [loading, setloading] = useState<any>(false)
  const [suppHeader, setSuppHeader] = useState<any>({})
  const [supplierDetails, setSupplierDetails] = useState<any[]>([])
  const [supplierPO, setSupplierPO] = useState<any[]>([])

  const [loadingPOItems, setloadingPOItems] = useState<any>(false)

  const [poitems, setPoitems] = useState<any[]>([])
  const [PONumberSelected, setPONumberSelected] = useState<any>('')

  useEffect(() => {
    let data = props.location.state

    getSupplier(data)
  }, [])

  const getSupplier=(data: any)=> {
    if (data == undefined) {
      data = {
        supplierid: '13195',
        productid: '26961',
      }
    }

    try {
      fetch(
        `${mainConfig.MAIN_ENDPOINT}/api/get-supplier-product-pc-po-data/${
          data ? data.supplierid : ''
        }/${data ? data.productid : ''}`,
        {
          method: 'get',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
        }
      )
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
            setSuppHeader(data.data.supplier)

            setSupplierDetails(data.data.supplier_with_product)

            setSupplierPO(data.data.supplier_with_po)

            setloading(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {}
  }

  const OnPoSelected=(data: any)=> {
    const download_csv_button = document.getElementById('csv-button')

    if (download_csv_button?.style.display == 'none') {
      download_csv_button.style.display = 'block'
    }

    let temp = data

    setloadingPOItems(true)

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-poitems/${temp.po_id}`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            setloading(false)

            return
          }
          response.json().then(function (data) {
            setPoitems(data.data)

            setPONumberSelected(temp.po_num)

            setloadingPOItems(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      setloading(false)
    }
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <React.Fragment>
      {loading == true ? (
        <div style={{marginLeft: '50%'}}>
          <div className='spinner-border m-2' role='status'>
            <span className='sr-only'>Loading...</span>
          </div>
        </div>
      ) : (
        <React.Fragment>
          <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
            <Link to='/home' className='pe-1 cursor-pointer'>
              <li className='breadcrumb-item pe-1'>Home</li>
            </Link>
            / &nbsp;
            <Link to='/product' className='pe-1 cursor-pointer'>
              <li className='breadcrumb-item pe-1'>Product</li>
            </Link>
            / &nbsp;<li className='breadcrumb-item pe-3'>Supplier-Product Details</li>
          </ol>
          <PageTitle breadcrumbs={[]}>{intl.formatMessage({id: 'MENU.DASHBOARD'})}</PageTitle>

          <div className='row gy-5 g-xl-10' style={{marginTop: '1%'}}>
            <div className='col-xl-6'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-7'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2' style={{alignSelf: 'center'}}>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {suppHeader.Supplier_Code}
                        </span>
                        <span className='text-muted mt-1 fw-bold fs-8'>Supplier Code</span>
                      </h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className='col-xl-6'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-7'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2' style={{alignSelf: 'center'}}>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {suppHeader.AC4}
                        </span>
                        <span className='text-muted mt-1 fw-bold fs-8'>Aggregate Code</span>
                      </h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div> 

          <div className='row g-5 gx-xxl-8'>
            <div className='col-xxl-12'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2'>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          Supplier-Products Details
                        </span>
                      </h3>
                      <div className='card-toolbar'></div>
                    </div>

                    <div className='card-body py-3 '>
                      <div className='table-responsive h-350px'>
                        <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4'>
                          <thead>
                            <tr className='fw-bolder text-muted'>
                              <th className='w-10px'></th>
                              <th className='min-w-100px'>Aggregate Code</th>
                              <th className='min-w-100px'>Product Code</th>
                              <th className='min-w-100px'>Description</th>
                              <th className='min-w-100px'>Price</th>
                              <th className='min-w-100px'>Price From Date</th>
                              <th className='min-w-80px'>Forecast</th>
                              <th className='min-w-70px'>Comment</th>
                            </tr>
                          </thead>

                          <tbody>
                            {supplierDetails != undefined &&
                              supplierDetails.map((j) => {
                                return (
                                  <tr>
                                    <td></td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.productcode}
                                      </span>
                                    </td>
                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.child_code ? j.child_code : '-'}
                                      </span>
                                    </td>
                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.description}
                                      </span>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.price ? j.price : '-'}
                                      </span>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.price_from_date
                                          ? moment(j.price_from_date).format('DD-MMMM-YYYY')
                                          : '-'}
                                      </span>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.forecast ? j.forecast : '-'}
                                      </span>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.comments ? j.comments : '-'}
                                      </span>
                                    </td>
                                  </tr>
                                )
                              })}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className='row g-5 gx-xxl-8'>
            <div className='col-xxl-12'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='row g-5 gx-xxl-8' style={{marginTop: '1%'}}>
                      <div className='col-xxl-12'>
                        <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                          <div className='row gy-5 g-xl-4'>
                            <div className={`card`}>
                              <div className='card-header border-0 pt-2'>
                                <h3 className='card-title align-items-start flex-column'>
                                  <span
                                    className='card-label fw-bolder fs-3 mb-1'
                                    style={{color: 'rgb(138, 38, 80)'}}
                                  >
                                    PO List
                                  </span>
                                </h3>

                                <div
                                  className='card-toolbar'
                                  data-bs-toggle='tooltip'
                                  data-bs-placement='top'
                                  data-bs-trigger='hover'
                                  title='Click to Download List as CSV'
                                  style={{display: 'none'}}
                                  id='csv-button'
                                >
                                  <button type='button' className='btn btn-sm btn-primary'>
                                    <KTSVG
                                      path='/media/icons/duotune/arrows/arr065.svg'
                                      className='svg-icon-2'
                                    />
                                  </button>
                                </div>

                                <div
                                  className='card-toolbar'
                                  data-bs-toggle='tooltip'
                                  data-bs-placement='top'
                                  data-bs-trigger='hover'
                                  title='Click to Create New PO'
                                ></div>
                              </div>

                              <div className='card-body py-3 '>
                                <div className='tab-content' id='myTabContent'>
                                  <div
                                    className='tab-pane fade show active'
                                    id='latestPO'
                                    role='tabpanel'
                                  >
                                    <Row>
                                      <Col span={12}>
                                        <div className='table-responsive h-350px table-scroll'>
                                          <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                                            <thead>
                                              <tr className='fw-bolder text-muted'>
                                                <th className='min-w-50px'>PO-Number</th>
                                                <th className='min-w-100px'>Raised By</th>
                                                <th className='min-w-100px'>Raised At</th>
                                                <th className='min-w-100px'>Status</th>
                                              </tr>
                                            </thead>

                                            <tbody>
                                              {supplierPO != undefined &&
                                                supplierPO.map((j) => {
                                                  return (
                                                    <tr>
                                                      <td>
                                                        <span
                                                          className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer text-decoration-underline link'
                                                          onClick={() => OnPoSelected(j)}
                                                        >
                                                          {j.po_num}
                                                        </span>
                                                      </td>
                                                      <td>
                                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                          {j.created_by ? j.created_by : '-'}
                                                        </span>
                                                      </td>
                                                      <td>
                                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                          {j.created_at
                                                            ? moment(j.created_at).format(
                                                                'DD-MMM-YYYY'
                                                              )
                                                            : '-'}
                                                        </span>
                                                      </td>

                                                      <td>
                                                        <div className='d-flex flex-shrink-0'>
                                                          {j.status == '0' ? (
                                                            <button
                                                              className='btn btn-text btn-color-light btn-bg-warning fw-bold btn-sm me-1 min-w-100px'
                                                              title='Mark PO as Completed'
                                                            >
                                                              {'In Progress'}
                                                            </button>
                                                          ) : j.status == '1' ? (
                                                            <button
                                                              className='btn btn-text btn-color-light btn-bg-success fw-bold btn-sm me-1 min-w-100px'
                                                              title='Revert To Pending'
                                                            >
                                                              {'Completed'}
                                                            </button>
                                                          ) : j.status == '2' ? (
                                                            <button
                                                              className='btn btn-text btn-color-light btn-bg-danger fw-bold btn-sm me-1 min-w-100px'
                                                              title='Revert To Pending'
                                                            >
                                                              {'Discarded'}
                                                            </button>
                                                          ) : (
                                                            '-'
                                                          )}
                                                        </div>
                                                      </td>
                                                    </tr>
                                                  )
                                                })}
                                            </tbody>
                                          </table>
                                        </div>
                                      </Col>
                                      <Col span={1}></Col>

                                      <Col span={11}>
                                        <div className='table-responsive h-350px'>
                                          {loadingPOItems ? (
                                            <div style={{marginLeft: '50%'}}>
                                              <div className='spinner-border m-2' role='status'>
                                                <span className='sr-only'>Loading...</span>
                                              </div>
                                            </div>
                                          ) : (
                                            <>
                                              {supplierPO.length > 0 && (
                                                <Button
                                                  type='dashed'
                                                  style={{
                                                    color: 'rgb(138, 38, 80)',
                                                    cursor: 'text',
                                                  }}
                                                >
                                                  {PONumberSelected}
                                                </Button>
                                              )}
                                              <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4'>
                                                <thead>
                                                  <tr className='fw-bolder text-muted'>
                                                    <th className='w-20px'></th>

                                                    <th className='min-w-100px'>Supplier</th>
                                                    <th className='min-w-100px'>Product Code</th>
                                                    <th className='min-w-70px cursor-pointer'>
                                                      Quantity
                                                    </th>
                                                    <th className='min-w-70px cursor-pointer'>
                                                      Price
                                                    </th>
                                                  </tr>
                                                </thead>

                                                <tbody>
                                                  {poitems != undefined &&
                                                    poitems.map((j) => {
                                                      return (
                                                        <tr>
                                                          <td></td>

                                                          <td>
                                                            <span className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer text-decoration-underline link'>
                                                              {j.supplier}
                                                            </span>
                                                          </td>
                                                          <td>
                                                            <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                              {j.product_code
                                                                ? j.product_code
                                                                : '-'}
                                                            </span>
                                                          </td>
                                                          <td>
                                                            <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                              {j.quantity}
                                                            </span>
                                                          </td>

                                                          <td>
                                                            <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                              {j.price.charAt(0) == '.'
                                                                ? '0' + j.price
                                                                : j.price}
                                                            </span>
                                                          </td>
                                                        </tr>
                                                      )
                                                    })}
                                                </tbody>
                                              </table>
                                            </>
                                          )}
                                        </div>
                                      </Col>
                                    </Row>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

       
          <div className='modal fade' id='po' aria-hidden='true'>
            <div className='modal-dialog mw-650px'>
              <div className='modal-content'>
                <div className='modal-header pb-0 border-0 justify-content-end'>
                  <div
                    className='btn btn-sm btn-icon btn-active-color-primary'
                    data-bs-dismiss='modal'
                  >
                    <KTSVG path='/media/icons/duotune/arrows/arr061.svg' className='svg-icon-1' />
                  </div>
                </div>

                <div className='modal-body scroll-y mx-5 mx-xl-18 pt-0 pb-15'>
                  <div className='text-center mb-13'>
                    <h1 className='mb-3'>Purchase Order</h1>
                  </div>
                  <form className='form'>
                    <div className='fv-row mb-10'>
                      <label className='form-label fs-6 fw-bolder text-dark text-left'>
                        Quantity :
                      </label>
                      <input
                        type='email'
                        className='form-control form-control-solid'
                        placeholder=''
                      />
                    </div>

                    <div className='fv-row mb-10'>
                      <label className='form-label fs-6 fw-bolder text-dark text-left'>
                        Price :
                      </label>
                      <input
                        type='email'
                        className='form-control form-control-solid'
                        placeholder=''
                      />
                    </div>

                    <button
                      type='submit'
                      id='kt_sign_in_submit'
                      className='btn btn-lg btn-primary w-40 mb-5'
                    >
                      <span className='indicator-label'>Submit</span>
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(ProductSupplierWrapper)
